import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-go-to',
  templateUrl: './go-to.page.html',
  styleUrls: ['./go-to.page.scss'],
})
export class GoToPage implements OnInit {

  constructor(
    public route : ActivatedRoute,
    private iab: InAppBrowser,
    public router : Router


  ) { 
    this.analyzeParameters();
  }

  ngOnInit() {
    //this.analyzeParameters();
  }


  analyzeParameters(){
    console.log('run analyzeParameters')
    if(this.route){
      if(this.route.snapshot.queryParamMap.get('url')){
        let url = this.route.snapshot.queryParamMap.get('url')
        const browser = this.iab.create(url);
        //return;
      }
    }
    console.log('no url');
    this.router.navigateByUrl('/');
  }

}
